import mergeStyle from './mergeStyle';
/**
 *  style item
 * @param inputItem  { className, style } 
 * @param defaultItem  styleItem  useStyles 
 */

export default function mergeStyleItem(inputItem, defaultItem) {
  var className = defaultItem.className;

  if (inputItem.className) {
    className = "".concat(inputItem.className, " ").concat(className);
  }

  return {
    className: className,
    style: mergeStyle(defaultItem.style, inputItem.style)
  };
}