import React, { memo, useCallback, useState } from 'react';
import { Button, Form, InputNumber, toast, Upload } from '@qunhe/muya-ui';
import { UploadIcon } from '@qunhe/muya-theme-light';
import FileSaver from 'file-saver';
import JSZip from 'jszip';

import { collectData } from '@common/core/point';

import { formatSizeUnits } from '@common/utils';

import './index.scss';

const LargeFileSegmentationPage = () => {
  const [loading, setLoading] = useState(false);

  const handleSubmit = useCallback((values: any) => {
    const {
      count,
      file: [targetFile],
    } = values;
    const file = targetFile.originFile as File;
    const [fileName, fileEnd] = file.name.split('.');
    setLoading(true);
    const fileReader = new FileReader();
    fileReader.readAsText(file);
    fileReader.onload = () => {
      try {
        const fileTextArray = (fileReader.result as string).split('\n');
        const fileTextLength = fileTextArray.length;
        const fileDataLength = Math.floor(fileTextLength / count);
        let index = 1;
        const zip = new JSZip();
        for (let i = 0; i < fileTextLength; i += fileDataLength) {
          // 
          zip.file(`${fileName}-${index++}.${fileEnd}`, fileTextArray.slice(i, i + fileDataLength).join('\n'));
        }
        // 
        zip.generateAsync({ type: 'blob' }).then((content) => {
          FileSaver.saveAs(content, `${fileName}.zip`);
          toast.success('~');
          // 
          collectData({
            key: 'LargeFileSegmentation',
            moduleName: 'Success',
            info: {
              fileName: file.name,
              fileSize: formatSizeUnits(file.size),
              count,
            },
          });
        });
      } catch {
        toast.error('~');
        // 
        collectData({
          key: 'LargeFileSegmentation',
          moduleName: 'Error',
          info: {
            fileName: file.name,
            fileSize: formatSizeUnits(file.size),
            count,
          },
        });
      } finally {
        setLoading(false);
      }
    };
  }, []);

  return (
    <div className="app">
      <Form defaultValues={{ count: 2 }} onSubmit={handleSubmit} labelWidth={100}>
        <Form.Item name="file" label="" rule={[{ type: 'any', required: true, message: '' }]}>
          <Upload multiple={false} request={(option) => ({ ...option, abort: () => {} })}>
            {({ getInputProps, getRootProps, getResultProps, uploadFiles }) => {
              return (
                <div style={{ width: 300 }}>
                  {!!uploadFiles?.length ? (
                    <div>
                      {uploadFiles.map((file) => (
                        <Upload.Result
                          key={file.uid}
                          style={{ width: '100%' }}
                          {...getResultProps({ file: { ...file, percent: 100, status: 'success' } })}
                        />
                      ))}
                    </div>
                  ) : (
                    <Upload.Card
                      {...getRootProps()}
                      width="100%"
                      height={80}
                      icon={<UploadIcon fontSize={24} />}
                      title=""
                    >
                      <input {...getInputProps()} />
                    </Upload.Card>
                  )}
                </div>
              );
            }}
          </Upload>
        </Form.Item>
        <Form.Item name="count" label="" rule={[{ type: 'number', required: true, message: '' }]}>
          <InputNumber style={{ width: 300 }} min={2} precision={0} placeholder="" />
        </Form.Item>
        <Form.Item>
          <Button htmlType="reset"></Button>
          <Button htmlType="submit" loading={loading} type="primary">
            
          </Button>
        </Form.Item>
      </Form>
    </div>
  );
};

export default memo(LargeFileSegmentationPage);
